.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: #fff;
}
.main-header{
  background-color: #0069d9;
  padding: 15px;
}
.main-wrapper{
  margin: 0 auto;
  width: 1200px;
}
.client-container{
  padding: 50px 20px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 20px;
}
.client-box{
  padding: 10px 14px;
  background-color: #fafafa;
  border-radius: 10px;
  box-shadow: #555  0 0 5px;
  position: relative;
}

.header-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-container p{
  margin: 0;
  font-weight: bold;
  font-size: 24px;
  color: #fff;
}
.login-btn{
  background-color:	#feff51;
  padding: 7px 20px;
  border-radius: 15px; 
  text-decoration: none;
  color: #000;
}
.logout-btn{
  color: #fff;
}
.admin-login-container{
  background-color: #0069d9;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.admin-login-container h1{
  display: block;
  margin-bottom: 10px;
}
.client-form-container{
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  top:0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.client-form{
  background-color: #fff;
  border-radius: 20px; 
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  position: relative;
}

.form-container{
  width: 400px;
  margin: 0 auto;
}

.login-form{
  margin-top: 10px;
  display: grid;
  grid-gap: 12px;
}

.login-form button,
.login-form input{
  padding: 15px;
  border-radius: 20px;
  outline: none;
  border: none;
}

.login-form button{
  background-color: #feff51;

}

.login-home-btn{
  color: #fff;
  margin-top: 10px;
  font-size: 14px;
  display: inline-block;
}

.client-form h2{
  margin-top: 0;
  margin-bottom: 20px;
}

.client-form input{
  display: block;
  padding: 10px 12px;
  border-radius: 10px; 
  margin: 10px 0;
  outline: none;
  border: 1px #ccc solid;
  width: 500px;
  z-index: 3;
}

.hide-form{
  display: none;
}

.close-btn{
  position: absolute;
  top: 10px;
  right: 10px;
}
.client-btn-wrapper{
  position: absolute;
  top: 5px;
  right: 5px;
  margin-bottom: 5px;
}
.client-btn{
  margin: 0 2px;
  color: #fff;
  background: #000;
  padding: 6px;
  border-radius: 20px;
}
.header-btn-wrapper,
.header-btn-wrapper > div{
  display: flex;
  color: #fff;
  align-items: center;
}
.header-btn-wrapper > div{
  margin-right: 25px;
}
.header-btn-wrapper p{
  font-size: 14px;
  margin-left: 5px;
}

.add-btn,
.close-btn,
.client-btn,
.logout-btn{
  cursor: pointer;
}

.sidebar{
  position: fixed; 
  width: 250px; 
  height: 100vh; 
  background-color: #dedede;
  z-index: 5;
  display: none;
}